@import url(https://fonts.googleapis.com/css?family=Rubik:300,400,700,900&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  height: 100%; }

body {
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Rubik', sans-serif; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }


.header {
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  justify-content: center; }
  .header__logo {
    max-width: 250px;
    justify-self: flex-end;
    padding: 20px; }

.about {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  border: dashed 0.5px #770872;
  padding: 30px 0; }
  .about__content {
    flex-basis: 70%;
    display: flex;
    flex-direction: column;
    margin: 20px; }
  .about__banner {
    width: 380px;
    align-self: center; }
  .about__text span {
    color: #e0630f;
    font-size: 2em; }
  .about__me {
    max-width: 350px;
    border: solid 1.5px black;
    box-shadow: 11px 14px 9px -1px rgba(0, 0, 0, 0.52);
    border-radius: 7px; }

.slider-button-lc {
  border: unset;
  background-color: unset;
  width: 10rem;
  height: 3em;
  color: white;
  font-size: 100%;
  font-weight: 700;
  border: 1.5px solid white;
  border-radius: 5px;
  margin: .2em; }
  .slider-button-lc:hover {
    background-color: #ffffff;
    color: black; }

.project-card {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-basis: 100%;
  flex-direction: column;
  color: white;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center; }

.project-contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: auto auto;
  height: 80%;
  width: 85%;
  border-radius: 15px;
  padding: 2rem;
  border: 1px solid black;
  background: #1f0534;
  background: -webkit-gradient(linear, left bottom, left top, from(#1f0534), color-stop(97%, #461071), to(#6d488a));
  background: -webkit-linear-gradient(bottom, #1f0534 0%, #461071 97%, #6d488a 100%);
  background: linear-gradient(0deg, #1f0534 0%, #461071 97%, #6d488a 100%); }
  .project-contents ul {
    list-style: circle;
    padding: 2rem; }

.customized-swiper-container {
  display: block;
  width: 100%;
  height: 450px;
  overflow: hidden; }

.swiper-pagination-bullet {
  width: 15px;
  height: 15px; }

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  height: 250px;
  width: 100%;
  background: url(/react_portfolio/static/media/nut.a868e088.svg) top 10px left 10px/15px no-repeat, url(/react_portfolio/static/media/nut.a868e088.svg) top 10px right 10px/15px no-repeat, url(/react_portfolio/static/media/nut.a868e088.svg) bottom 10px right 10px/15px no-repeat, url(/react_portfolio/static/media/nut.a868e088.svg) bottom 10px left 10px/15px no-repeat black; }
  .footer__logo {
    max-width: 250px;
    padding: 10px; }

.page {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column; }

.swipecontainer {
  width: 100%;
  height: 450px;
  margin: 20px auto;
  position: relative; }

